import React, { Component } from 'react';
import $ from 'jquery';

class Header extends Component {

  componentDidMount() {

    $(document).ready(function(){

      $(window).scroll(function(){
        if($(this).scrollTop() > 1) {
          $('.header_wrapper').addClass('on');
          $('.header_wrapper').find('.logo').find('img').attr('src', 'common/images/common/logo_cme.png');
        } else {
          $('.header_wrapper').removeClass('on');
          $('.header_wrapper').find('.logo').find('img').attr('src', 'common/images/common/logo_cme.png');
        }
      });

      // mobile_gnb
      $('.m_gnb').click(function(){
        $('.gnb_wrap').addClass('on').addClass('mobile');
      });
      $('.m_gnb_close').click(function(){
        $('.gnb_wrap').removeClass('on').removeClass('mobile');
      });
      $('.gnb_background').click(function(){
        $('.gnb_wrap').removeClass('on').removeClass('mobile');
      });
      $('.gnb_wrap.mobile').find('a').click(function(){
        $('.gnb_wrap').removeClass('on').removeClass('mobile');
      });

      $(window).resize(function(){
        var windowWidth = $(window).width();

        if(windowWidth < 961) {
          $('.header_wrapper').find('.logo').find('img').attr('src', 'common/images/common/logo_cme.png');
        }
      });

    });

  }

  render() {

    return (

      <header className="header_wrapper">

        <div className="inner">
          <div className="flex_wrap space lft">
            <div className="flex_con">
              <h1 className="logo">
                <a href="/"><img src="common/images/common/logo_cme.png" alt="CME" /><span className="logo_txt"> <br/></span></a>
              </h1>
            </div>
            <div className="flex_con">
              <a href="javascript:void(0);" className="m_gnb"><i></i></a>
              <div className="gnb_wrap">
                <ul className="gnb_list">
                  <li className="depth1"><a href="/#/introduce"></a></li>
                  <li className="depth1"><a href="/#/pfList"></a></li>
                  {/* <li className="depth1"><a href="/#/contact"></a></li> */}
                </ul>
                <a href="javascript:void(0);" className="m_gnb_close"><i></i></a>
                <div className="gnb_background"></div>
              </div>
            </div>
          </div>
        </div>

      </header>

    )
  }
}

export default Header;
