import React from 'react';
import { HashRouter, Route, Switch, Router } from 'react-router-dom';
import MainHeader from './include/MainHeader';
import SubHeader from './include/SubHeader';
import Footer from './include/Footer';
import Location from './include/Location';
import QuickBanner from './component/QuickBanner';
import Main from './main/Main';
import PfList from './sub/PfList';
import Introduce from './sub/Introduce';
import Contact from './sub/Contact';
import NotFound from './error/NotFound';

function App() {

  return (
    <HashRouter>
      <Switch>
        <Route path="/" exact={true}>
          <MainHeader />
          <Main />
        </Route>
        <Route path="/pfList">
          <SubHeader />
          <Location />
          <PfList />
        </Route>
        <Route path="/introduce">
          <SubHeader />
          <Location />
          <Introduce />
        </Route>
        <Route path="/contact">
          <SubHeader />
          <Location />
          <Contact />
        </Route>
        {/*     */}
        <Route path="*">
          <SubHeader />
          <NotFound />
        </Route>
      </Switch>
      <Footer />
      <QuickBanner />
    </HashRouter>
  );

};

export default App;
